<template>
  <div class="Upload demo-section">
    <div class="demo-header" >
      <h1>Loader {{ color }}</h1>
    </div>

    <div class="demo-text">
      <div class="loader" v-bind:style="{ width:size, height:size }" v-cloak>
        <span v-bind:style="{ background:color  }"></span>
        <span v-bind:style="{ background:color1 }"></span>
        <span v-bind:style="{ background:color2 }" v-if=" (type == 'bg') "></span>
      </div>
    </div>

    <input type="text" name="name" v-model="color" placeholder="ex: #50514f">
  </div>
</template>

<script>
/* eslint-disable */
import colorPicker from 'color';

export default {
  props: {
    'size': '',
    'color': '',
    'type': ''
  },
  data() {
    return {
      color1: '50514f',
      color2: '50514f',
    };
  },
  computed: {
      color1: function() {
        this.color2 = colorPicker(this.color).darken(0.3).rgbString();
        return colorPicker(this.color).darken(0.15).rgbString()
      },
  },
};
/* eslint-enable */
</script>

<style scoped lang="scss">
@import "../scss/app.scss";

.demo-text {
  width: 100%;
  text-align: center;
  @include flexStyle(row, flex-start, center);
  flex-wrap: wrap;
  .loader {
    margin: 0.25rem 0.5rem;
  }
}
</style>



